import { TiLocale } from '@cloud/tiny3';
import { currencyCNY, defaulten_US, defaultzh_CN } from '@cloud/tinyplus3/locale/i18n';
import { siteConfig } from './siteConfig';

/**
 * 项目构建，需要调用某个局点的初始化
 * import { TpConfig } from "@cloud/tinyplus3/locale/hws/TpConfig";
 * TpConfig.init();
 */
export class TpConfig {
    public static init(language: string = 'zh-CN'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'zh-CN': { ...words['zh-CN'], ...defaultzh_CN, ...currencyCNY, ...siteConfig },
            'en-US': { ...words['en-US'], ...defaulten_US, ...currencyCNY, ...siteConfig }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
